/**
 * 获取图层样式的函数。
 *
 * @param {object} options - 选项对象
 * @return {object} 更新后的选项对象
 */
function getLayerStyle(options) {
  let style = options.style; //cogtifLayer图层样式
  // this.layerConfig = JSON.parse(JSON.stringify(options))
  if (options.style) {
    // 设置cogtifLayer图层样式
    if (options.source) {
      if (!options.source.normalize) {
        // this.scala = style.scala;
        let bandsNumber = style.bandsNumber;
        let offset = Number(style.offset) || 0;
        let color = [];
        // uv 通道的渲染
        if (bandsNumber == 2) {

          let value1 = ['-', ["band", 1], offset];
          let value2 = ['-', ["band", 2], offset];
          let n1 = [
            "+",
            ["*", value1, value1],
            ["*", value2, value2],
          ];
          color = handlerColor(style.color, style.scala, style.radio);
          if (style.radio == 1) {
            let colorGradient = color[2];
            colorGradient[2] = ["sqrt", n1]
          } else {
            for (let i = 3; i < color.length - 1; i = i + 2) {
              color[i][1] = ["sqrt", n1];
            }
          }
          // console.log("color--渲染样式-----------------");
          // console.log(JSON.stringify(color));
        } else if (bandsNumber == 1) {

          if (style.unit == "K") {

            color = handlerColor(style.color, style.scala, style.radio);

          } else if (style.unit == "℃") {

            let value1 = ['/', ["band", 1], style.scala];
            let n1 = ['-', value1, 273.15];


            color = handlerColor(style.color, style.scala / 10, style.radio);
            if (style.radio == 1) {
              color[2][2] = n1
            } else {
              for (let i = 3; i < color.length - 1; i = i + 2) {
                color[i][1] = n1;
              }
            }
          } else {
            color = handlerColor(style.color, style.scala, style.radio);
          }
        } else {
          color = handlerColor(style.color, style.scala, style.radio);
        }

        // color = handlerColor(style.color, style.scala, style.radio);
        options.style.color = color;
      } else {
        options.style = null;
      }
    } else {
      options.style = null;
    }
  }
  return options;
}

function getUpdateLayerStyle(options) {

  // this.scala = 1; //cogtifLayer图层样式scala 值
  // this.map = options.map; //map图层的参数相关信息
  let style = options.style; //cogtifLayer图层样式
  // this.layerConfig = JSON.parse(JSON.stringify(options))
  if (options.style) {
    options.style.color = handlerColor(style.color, style.scala, style.radio);

    // console.log(JSON.stringify(options.style.color))

  }
  return options;
}

/**
 * 递归地将两个对象合并为一个新对象，新对象包含两个对象中的所有属性。
 * 如果一个属性在两个对象中都存在，并且其值是一个对象，则函数会递归合并这两个对象。
 * 如果一个属性只存在于其中一个对象中，则将其添加到合并后的对象中。
 * 如果一个属性在两个对象中都存在，并且其值不是一个对象，则第二个对象中的值将覆盖第一个对象中的值。
 *
 * @param {Object} obj1 - 要合并的第一个对象。
 * @param {Object} obj2 - 要合并的第二个对象。
 * @return {Object} 合并后的对象。
 */
// function mergeObjects(obj1, obj2) {
//   // 创建一个新的对象来存储合并后的结果
//   const mergedObj = {};
//   // 遍历第一个对象的属性
//   for (let prop in obj1) {
//     // 如果属性值是对象，则递归调用mergeObjects函数
//     if (typeof obj1[prop] === 'object' && typeof obj2[prop] === 'object') {
//       mergedObj[prop] = mergeObjects(obj1[prop], obj2[prop]);
//     } else {
//       mergedObj[prop] = obj1[prop];
//     }
//   }

//   // 遍历第二个对象的属性
//   for (let prop in obj2) {
//     // 如果属性值是对象，且在第一个对象中不存在对应的属性，则直接添加到合并后的对象中
//     if (typeof obj2[prop] === 'object' && !obj1[prop]) {
//       mergedObj[prop] = obj2[prop];
//     } else {
//       // 否则，如果属性已经存在于mergedObj中，将被obj2中的值覆盖
//       // mergedObj[prop] = obj2[prop];
//       mergedObj[prop] = Object.assign({},  mergedObj[prop], obj2[prop])
//     }
//   }
//   return mergedObj;
// }

function mergeObjects(obj1, obj2) {
  const mergedObj = {};

  // 遍历obj1的根属性
  for (let prop in obj1) {
    if (obj1.hasOwnProperty(prop)) {
      // 如果属性值是对象，则进行合并
      if (typeof obj1[prop] === 'object' && obj1[prop] !== null) {
        mergedObj[prop] = Object.assign({}, obj1[prop]);
      } else {
        mergedObj[prop] = obj1[prop];
      }
    }
  }

  // 遍历obj2的根属性
  for (let prop in obj2) {
    if (obj2.hasOwnProperty(prop)) {
      // 如果属性在mergedObj中已存在，使用Object.assign()合并值
      if (mergedObj.hasOwnProperty(prop) && typeof mergedObj[prop] === 'object' && mergedObj[prop] !== null) {
        mergedObj[prop] = Object.assign({}, mergedObj[prop], obj2[prop]);
      } else {
        // 否则直接赋值
        mergedObj[prop] = obj2[prop];
      }
    }
  }

  return mergedObj;
}


