import GeoTIFF from '../../source/GeoTIFF.js'; // source/GeoTIFF.js';
import TileLayer from '../../../layer/WebGLTile.js';

// import { transform } from "../../../proj.js";

// 处理数据使用
import { getLayerStyle, mergeObjects } from "../common/commonLayerUnit.js";

// import { getCofTifData } from "./cogtifWorld.js"
// import GeoTIFF from '../../../source/GeoTIFF.js';
// import TileLayer from '../../../layer/WebGLTile.js';
// import { transform } from "../../../proj.js";
// import { fromUrl, Pool } from "geotiff";
// import { fromUrl, Pool } from "geotiff";
var cme_cogtifLayer2 = class cogtifLayerClass2 extends TileLayer {
  constructor() {
    let options = {};
    super(options);
    this.legendLimit = 250;
  }
  /**
   * 渲染选项并返回一个解析为true的Promise。
   *
   * @param {Object} options - 要渲染的选项。
   * @return {Promise} 一个解析为true的Promise。
   */
  _render(parfams) {
    let options = JSON.parse(JSON.stringify(parfams));
    let that = this;
    // if (Array.isArray(options?.style?.color)) {
    //   options.style.color.splice(this.legendLimit);
    // }
    if (options.Greater) {
      options.style = this.getStyleOpactiy(options.Greater, options.style)
    }
    if (options.style) {
      if (options.style.color) {
        options.orignColor = JSON.parse(JSON.stringify(options.style.color));
      } else {
        options.orignColor = {};
      }
    }
    if (!options.style) {
      throw Error('"options.style"参数缺失');
    }
    options.CustomStyle = options.style;
    if (!options.source) {
      throw Error('"options.source"参数缺失');
    }
    return new Promise((resolve) => {
      const sourceObj = new GeoTIFF(options.source);
      sourceObj.getView().then((viewConfig) => {
        const metas = sourceObj.getMetaDatas();
        const { scale, scala, offset, unit } = options.style;
        const proj = {
          extent: metas.extent,
          projection: metas.proj || "EPSG:4326",
          unit: unit || metas.unit || ''
        };
        Object.assign(options.style, {
          bandsNumber: +metas.bandCount || 1,
          offset: offset || +metas.offset || 0,
          scala: scale || scala || +metas.scale || 1,
          unit: unit || metas.unit || ''
        });
        options.proj = proj;
        // @ts-ignore
        if (options.style.color) {
          options = getLayerStyle(options);
        }
        that.options = JSON.parse(JSON.stringify(options));// options;
        that.style_ = options.style;
        // 设置属性和数据源
        sourceObj.values_ = options.source;
        that.setProperties({ ...options, source: sourceObj });
        that.once('postrender', () => resolve(metas));

        // //  that.once('sourceready', function (event) {})
        // //postcompose
        // //rendercomplete
        // that.once("postcompose", function (event) {

        //   resolve(true);
        // })

        // that.setSource(options.source)
        // that.setProperties({ CustomStyle: optionsStr.CustomStyle });
        // that.updateStyleVariables({ style: optionsStr.CustomStyle })
      })
    }
    )
  }

  /**
   * 设置函数的来源。
   *
   * @param {type} paramName - 参数的描述
   * @return {type} 返回值的描述
   */
  _setSource(options) {
    let params = JSON.parse(JSON.stringify(options))
    let that = this;
    if (params.source == null) {
      return new Promise((resolve) => {
        resolve(true);
      });
    }
    return new Promise((resolve) => {
      if (params.source) {
        params.source = new GeoTIFF(params.source);
      } else {
        params.source = new GeoTIFF();
      }
      params.source.getView().then((viewConfig) => {

        let projCode = "EPSG:4326";
        if (viewConfig.projection) {
          projCode = viewConfig.projection.getCode()

        }
        //   // projCode=viewConfig.projection.getCode()
        // let proj={
        //   extent:viewConfig.extent,
        //   projection: projCode,//viewConfig.projection.getCode(),
        // }
        // // let proj = {
        // //   extent: viewConfig.extent,
        // //   projection: viewConfig.projection.getCode(),
        // // };
        // that.setProperties({
        //   proj: proj
        // });


        that.once("sourceready", function (event) {
          resolve(true);
        });
        that.setSource(params.source);
      });
    });
  }


  _setSourceMap(map, options) {
    // return this.setTempLayer(map).then((viewConfig) => {

    console.log("CME2D")
    // console.log(options.source.sources[0].url);
    let params = JSON.parse(JSON.stringify(options));
    console.log(params.source.sources[0].url);

    let that = this;
    if (params.source == null) {
      return new Promise((resolve) => {
        resolve(true);
      });
    }
    return new Promise((resolve) => {
      if (params.source) {

        params.source = new GeoTIFF(params.source);
      } else {
        params.source = new GeoTIFF();
      }
      params.source.getView().then((viewConfig) => {

        let projCode = "EPSG:4326";
        if (viewConfig.projection) {
          projCode = viewConfig.projection.getCode()

        }
        map.once("rendercomplete", function () {
          // that.cogtifLayer.setOpacity(0);
          // console.log("图层更新source加载完成")
          resolve(true);
        });
        that.setSource(params.source);
      });
    });
    // });
  }
  /**
   * 对整个函数的描述。
   *
   * @param {type} paramName - 参数描述
   * @return {type} 返回值描述
   */
  _setStyle(options) {
    // if (Array.isArray(options?.style?.color)) {
    //   options.style.color.splice(this.legendLimit);
    // }
    if (options.Greater) {
      options.style = this.getStyleOpactiy(options.Greater, options.style)
    }
    if (options.style) {
      if (options.style.color) {
        this.options.orignColor = options.style.color;
      } else {
        options.style.color = JSON.parse(JSON.stringify(this.options.orignColor));
      }
    } else {
      options.style.color = JSON.parse(JSON.stringify(this.options.orignColor));
    }
    return new Promise((resolve) => {
      if (options.source && ("interpolate" in options.source)) {
        const source = this.getSource();
        const properties = source.getProperties();
        if (properties.interpolate != options.source.interpolate) {
          source.set("interpolate", options.source.interpolate);
          let properties = JSON.parse(JSON.stringify(properties));
          // properties.source.interpolate = options.source.interpolate;
          this.setSource(new GeoTIFF(properties));
          source.setProperties(properties)
        }
      }
      this.options = getLayerStyle(mergeObjects(this.options, options));
      this.style_ = this.options.style;
      this.options.CustomStyle = this.options.style; //    options.style;
      const newStyle = JSON.parse(JSON.stringify(this.options.style));
      this.setStyle(newStyle);
      resolve(true);
    });
  }

  getStyleOpactiy(params, _style) {
    let style = JSON.parse(JSON.stringify(_style))
    if (params) {
      if (params[0] == 0) {
        let minValue = style.color[0][0];
        minValue = minValue - 0.1;
        style.color.unshift([minValue, [0, 0, 0, 0]]);
        style.color.unshift([-9999, [0, 0, 0, 0]]);
      }
      //  else if(options.params[0]==0){

      //  }
      if (params[1] == 0) {

        let maxValue = style.color[style.color.length - 1][0];
        maxValue = maxValue + 0.1;
        style.color.push([maxValue, [0, 0, 0, 0]]);
      }
    }

    return style;

  }

  /**
   * 整个函数的描述。
   *
   */
  _remove() {
    this.dispose();
    // 变量清空
  }

  computedNewExtent(extent, worldNumber) {
    let mixLng = extent[0] + 360 * worldNumber;
    let maxLng = extent[2] + 360 * worldNumber;
    return [mixLng, extent[1], maxLng, extent[3]];
  }
  getSourceProperties(options) {

    let sourceOpitions = this.options.source.values_;
    for (let key in sourceOpitions) {
      if (sourceOpitions.hasOwnProperty(key)) { // 确保key是对象自身的属性
        for (let newKey in options.source) {
          if (options.source.hasOwnProperty(newKey)) {
            if (key == newKey)
              sourceOpitions[key] = options.source[newKey];
          }
        }
      }
    }
    let sourceCollection = []
    for (let newKey in sourceOpitions.sources) {
      if (sourceOpitions.sources.hasOwnProperty(newKey)) {
        sourceCollection.push(sourceOpitions.sources[newKey]);

      }
    }
    sourceOpitions.sources = sourceCollection;
    return sourceOpitions;
  }
}

export default cme_cogtifLayer2
